/* rubik-regular - latin */
@font-face {
  font-family: "RubikNormal";
  font-style: normal;
  font-weight: 400;
  src: url("../assets/fonts/rubik-v20-latin-regular.eot"); /* IE9 Compat Modes */
  src: local(""), url("../assets/fonts/rubik-v20-latin-regular.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("../assets/fonts/rubik-v20-latin-regular.woff2") format("woff2"),
    /* Super Modern Browsers */ url("../assets/fonts/rubik-v20-latin-regular.woff") format("woff"),
    /* Modern Browsers */ url("../assets/fonts/rubik-v20-latin-regular.ttf") format("truetype"),
    /* Safari, Android, iOS */ url("../assets/fonts/rubik-v20-latin-regular.svg#Rubik") format("svg"); /* Legacy iOS */
}
